@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-109xl: 128px;
  --font-size-17xl: 36px;
  --font-size-xl: 20px;
  --font-size-13xl: 32px;
  --font-size-45xl: 64px;
  --font-size-29xl: 48px;
  --font-size-5xl: 24px;
  --font-size-mini: 15px;
  --font-size-base: 16px;
  --font-size-21xl: 40px;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-royalblue: #254acd;
  --color-paleturquoise: #aaf0f4;
  --color-gray-400: #937b7b;
  --color-gray-100: #181717;
  --color-gray-200: #151414;
  --color-gray-500: #121111;
  --color-gray-300: #0c0b0b;
  --color-indianred: #d75959;
  --color-gainsboro-100: #d9d9d9;
  --color-gainsboro-200: rgba(217, 217, 217, 0.2);
  --color-silver: #c5c2c2;
  --color-lightcoral: #e18282;
  --color-mediumseagreen: #25bf88;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-5xs: 8px;

  /* border radiuses */
  --br-31xl: 50px;
  --br-xl: 20px;
}
